/*
 * Flex - Align Items
 */

.flex {
  &--align-items-start,
  &--align-items-flex-start {
    align-items: flex-start;
  }

  &--align-items-end,
  &--align-items-flex-end {
    align-items: flex-end;
  }

  &--align-items-center {
    align-items: center;
  }

  &--align-items-baseline {
    align-items: baseline;
  }
}
